.quoet {
  position: relative;
  display: flex;
  justify-content: space-between;
  color: $white;
  width: 100%;
  background: $red-shade;
  @extend %_cut_val-24;
  overflow: hidden;
  &__img {
    position: relative;
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    &:hover{
      .quoet-img__copyright{
      opacity: 1;
      }
    }
  }
  &__text-block {
    display: flex;
    flex-direction: column;
    padding: 5%;
    width: 100vw;
    &::before {
      content: '\201C';
      font-family: sans-serif;
      font-size: rem(72px);
      line-height: rem(22px);
    }
  }
  &__text {
    color: $white;
    font-style: italic;
    font-size: rem(22px);
    padding: 0;
    margin: 0;
    background: none;
  }
  &-img__copyright{
    position: absolute;
    margin: 0;
    bottom: 1%;
    left: 1%;
    color: $black-pastel;
    padding: rem(5px);
    opacity: 0;
    background: $black-origin;
  }
  &__autor {
    &::before {
      content: '\2014';
      padding: 0 rem(15px) 0 0;
    }
  }
}